import {
	Box,
	Container,
	Divider,
	Grid,
	Stack,
	Typography,
	useMediaQuery,
	useTheme
} from "@mui/material"
import ProductContainer from "../../components/ProductContainer"
import DrIQLogo from "../../assets/svgs/DRIQ.svg"
import EZDoc from "../../assets/svgs/EZDOC.svg"
import EZPath from "../../assets/svgs/EZPATH.svg"
import EZAnalytics from "../../assets/svgs/EZANALYTICS.svg"
import EZNav from "../../assets/svgs/EZNAV.svg"
import { StyledTable, ActivityLog } from "../../components/StyedTable"
import useLocalStorage from "../../hooks/useLocalStorage"
import { useEffect } from "react"
import moment from "moment"
import { useNavigate } from "react-router-dom"

export const products = [
	{
		name: "Dr. iQ",
		icon: DrIQLogo
	},
	{
		name: "EZ DOC",
		icon: EZDoc
	},
	{
		name: "EZ PATH",
		icon: EZPath
	},
	{
		name: "EZ ANALYTICS",
		icon: EZAnalytics
	},
	{
		name: "EZ NAV",
		icon: EZNav
	},
	{
		name: "EZ WEB",
		icon: DrIQLogo,
		disabled: true
	},
	{
		name: "GP CHECKLIST",
		icon: DrIQLogo,
		disabled: true
	}
]

function Home() {
	const theme = useTheme()
	const breakpoint = useMediaQuery(theme.breakpoints.down("md"))
	console.log("breakpoint", breakpoint)
	return (
		<Container maxWidth="xl">
			<Box>
				<Grid container spacing={3}>
					<Grid item xs={12} md={8} order={breakpoint ? 2 : 1}>
						<Stack spacing={3}>
							<Typography fontWeight="bold" variant="h6">
								Products
							</Typography>
							<ProductContainer products={products} />
							<Stack spacing={2}>
								<Typography fontWeight="bold" variant="h6">
									Activity logs
								</Typography>
								<ActivityLog />
							</Stack>
						</Stack>
					</Grid>
					<Grid item xs={12} md={4} order={breakpoint ? 1 : 2}>
						<Box
							height="100%"
							width="100%"
							display="flex"
							flexDirection="column"
							gap={2}
						>
							<Box bgcolor="#d5e3ff" height="50%" borderRadius={4}>
								{/* Graphs1 */}
							</Box>
							<Box bgcolor="#d5e3ef" height="50%" borderRadius={4}>
								{/* Graphs2 */}
							</Box>
						</Box>
					</Grid>
				</Grid>
			</Box>
		</Container>
	)
}

export default Home
