import { Button, Grid, Typography, Box } from "@mui/material"
import React from "react"
import AuthCode from "react-auth-code-input"
import { SAuthContainer } from "../../pages/ResetPassword/index.styles"
import { Driq } from "../../svgs"
import { CheckCircleIcon } from "../../utils/Icons"
import IconContainer from "../IconContainer"
import InputStatus from "../InputStatus"
import StyledButton from "../StyledButton"
import StyledInput from "../StyledInput"

const AccountAccessItem = () => {
	return (
		<Grid container spacing={1}>
			<Grid xs={2} item>
				<IconContainer sx={{ marginLeft: "auto" }}>
					<Driq />
				</IconContainer>
			</Grid>
			<Grid container xs={10} alignItems="center" item>
				<Grid container item alignItems="center" spacing={2}>
					<Grid xs={10} item>
						<StyledInput
							error
							helperText={"hello"}
							// disabled
							placeholder="Email"
							defaultValue=""
							variant="outlined"
							type="text"
							fullWidth
						/>
					</Grid>
					<Grid alignItems="center" whiteSpace={"nowrap"} xs={2} item>
						<Button variant="text">Send OTP</Button>
					</Grid>
				</Grid>
				<Typography mt={0.1} variant="subtitle1">
					Enter the code we just sent to
					<Typography variant="body1" color="primary">
						username2@mail.com
					</Typography>
				</Typography>
				<Grid mt={0.1} container alignItems="center" item spacing={2}>
					<Grid xs={10} item>
						<SAuthContainer>
							<AuthCode
								isPassword
								ariaLabel="please enter the otp"
								autoFocus
								length={4}
								onChange={res => {
									console.log(res)
								}}
								containerClassName="auth-container"
								inputClassName="auth-input"
							/>
						</SAuthContainer>
					</Grid>
					<Grid md={2} item>
						<StyledButton size="large" variant="contained">
							Verify
						</StyledButton>
					</Grid>
				</Grid>
				<Grid xs item mt={1}>
					<Box>
						{/* <Typography component="span" color="text.mute">
							Don't receive the OTP?{"  "}
						</Typography>
						<Typography fontWeight="700" variant="subtitle2" component="span" color="primary">
							Resend OTP
						</Typography> */}
						<InputStatus
							icon={<CheckCircleIcon fontSize="small" color="success" />}
							text="OTP verified"
						/>
					</Box>
				</Grid>
			</Grid>
		</Grid>
	)
}

export default AccountAccessItem
