import { IconButton, InputAdornment, TextField } from "@mui/material"
import InputStatus from "../InputStatus"
import { VisibilityOff, Visibility } from "../../utils/Icons"
import { IStyledInput } from "./StyledInput.types"

const StyledInput = ({
	type,
	placeholder,
	fullWidth,
	error,
	helperText,
	...rest
}: IStyledInput) => {
	return (
		<TextField
			type={type}
			placeholder={placeholder}
			fullWidth={fullWidth}
			error={error}
			helperText={
				helperText ? (
					<InputStatus component="span" text={"Invalid password"} />
				) : null
			}
			InputProps={{
				endAdornment: type === "password" && (
					<InputAdornment position="end">
						<IconButton aria-label="toggle password visibility" edge="end">
							{false ? <VisibilityOff /> : <Visibility />}
						</IconButton>
					</InputAdornment>
				)
			}}
			{...rest}
		/>
	)
}

export default StyledInput
