import { Location } from 'react-router-dom'
import { History } from 'history'

export default function(location: Location, history?: History) {
  const split = location.pathname.split('/')
  const tail = split[split.length - 1]

  return trail[tail] || []
}

type TrailType = {
  label: string,
  link?: string
}

const trail: Record<string, TrailType[]> = {
  'user-management': [{label: 'Home', link: '/'}, {label: 'User Management'}],
  'profile': [{label: 'Home', link: '/'}, {label: 'Profile'}],
  'audit-trail': [{label: 'Home', link: '/'}, {label: 'User Management', link: 'user-management'}, {label: 'Audit Trail'}],
  'create-user': [{label: 'Home', link: '/'}, {label: 'User Management', link: 'user-management'}, {label: 'Create/Edit User'}]
}