import { Box, Card, Link, styled } from "@mui/material"

export const SProfileContainer = styled(Box)(() => ({
	mt: "auto",
	display: "grid",
	placeItems: "center",
}))

export const SProfileCard = styled(Box)(({theme}) => ({
	width: '100%',
	display: "flex",
	borderRadius: parseInt(`${theme.shape.borderRadius}`) * 8,
	// overflow: 'hidden'
	[theme.breakpoints.down('sm')]: {
		flexDirection: 'column'
	}
}))
export const SProfileCardContainer = styled(Card)(({ theme }) => ({
	width: '100%',
	display: "flex",
	flexDirection: "column",
	maxWidth: "940px",
	borderRadius: parseInt(`${theme.shape.borderRadius}`) * 8,
	overflow: 'hidden',
}))
export const SProfileSideBar = styled(Box)(({theme}) => ({
	width: "300px",
	background: theme.palette.background.backgroundGradientSingle?.('50deg'),
	display: "flex",
	flexDirection: "column",
	padding: "1rem",
	gap: "2rem",
	[theme.breakpoints.down('md')]: {
		width: 230
	},
	[theme.breakpoints.down('sm')]: {
		width: '100%'
	}
}))
export const SProfileImageContainer = styled(Box)(() => ({
	display: "grid",
	placeItems: "center",
	marginTop: "2rem",
	gap: "0.5rem",
}))
export const SProfileImage = styled(Box)(() => ({
	display: "flex",
	justifyContent: "center",
	alignItems: "center",
	width: 105,
	height: 105,
	backgroundColor: "white",
	borderRadius: "50%"
}))
export const SListContainer = styled(Box)(({ theme }) => ({
	display: "flex",
	flexDirection: "column",
	gap: "1rem",
	[theme.breakpoints.down('sm')]: {
		display: 'grid',
		gridAutoFlow: 'column',
		gridAutoColumns: '1fr',
		justifyContent: 'space-around',
	}
}))
export const SListItem = styled(Box)(({ theme }) => ({
	display: "flex",
	alignItems: "center",
	gap: "1rem",
	[theme.breakpoints.down('sm')]: {
		fontSize: '0.85em',
		flexDirection: "column",
	}
}))

export const SLoadingBar = styled(Box)(() => ({
	height: "2px",
}))
