import { styled, Box } from "@mui/material"

export const SAuthContainer = styled(Box)(({ theme: { palette } }) => ({
	"& .auth-container": {
		width: "100%",
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		gap: "0.25rem"
	},
	"& .auth-input": {
		maxWidth: "83px",
		// height: "54px",
		border: `2px solid ${palette.secondary["900"]}`,
		borderRadius: "4px",
		padding: 0,
		fontSize: "2rem",
		textAlign: "center",
		// textTransform: "uppercase",
		background: "white",
		fontFamily: "lato",
		backgroundClip: "padding-box"
	}
}))
