import { alpha, Card, styled } from "@mui/material"
import { ISCard } from "./ImageCard.types"

const colors: Record<string, string> = {
	"DR.iQ": "#d9ebff",
	"EZ DOC": "#ffd6ff",
	"EZ PATH": "#e1d5fd",
	"EZ ANALYTICS": "#fcd5ff",
	"EZ NAV": "#dcd0fa",
	"EZ WEB": "#d5e3ff",
	"EZ LOGIN": "#ecc6ff"
}

export const SCard = styled(Card, {
	//to control the prop injected to dom element
	shouldForwardProp: prop => prop !== "disabled"
})<ISCard>(({ theme: { palette }, disabled, name }) => ({
	display: "flex",
	flexDirection: "column",
	alignItems: "center",
	gap: 15,
	width: "115px",
	padding: "35px 0px",
	height: "149px",
	borderRadius: "25px",
	backgroundColor: name ? alpha(colors[name], 0.3) : "transparent",
	boxShadow: "none"
}))
