import { Box, Card } from "@mui/material"
import { LockIcon } from "../../utils/Icons"
import { IImageCard } from "./ImageCard.types"
import { SCard } from "./index.styles"

const ImageCard = ({ icon, disabled }: IImageCard) => {
	return (
		<>
			<Box
				sx={{
					position: "absolute",
					top: 7,
					right: 3
				}}
			>
				{disabled && <LockIcon fontSize="small" />}
			</Box>
			<Box sx={{}}>
				<img
					alt="Dr iq"
					width="50px"
					height="50px"
					src={`${process.env.REACT_APP_SERVER_URL}/${icon}`}
				/>
			</Box>
		</>
	)
}

export default ImageCard
