import { DevTool } from "@hookform/devtools"
import {
	Avatar,
	Badge,
	Box,
	IconButton,
	InputAdornment,
	InputLabel,
	LinearProgress,
	Link,
	Typography
} from "@mui/material"
import { AxiosError, AxiosResponse } from "axios"
import { FormProvider, useForm } from "react-hook-form"
import {
	useMutation,
	UseMutationResult,
	useQuery,
	useQueryClient,
	UseQueryResult
} from "react-query"
import { get, post, put } from "../../api/utils"
import FormInput from "../../components/FormInput/FormInput"
import StyledButton from "../../components/StyledButton"
import { useEffect } from "react"
import {
	SLoadingBar,
	SProfileCard,
	SProfileCardContainer,
	SProfileContainer
} from "./index.styles"
import { IFormInput, IGetUserInfo, IUserInfo } from "./Profile.types"
import { yupResolver } from "@hookform/resolvers/yup"
import { profileSchema } from "./ProfileValidations"
import Sidebar from "./Sidebar"
import MainContent from "./MainContent"

const values = {
	address: "",
	first_name: "",
	last_name: "",
	email: "",
	contact_no: "",
	profile_image: ""
}

function capitalize(s = "") {
	return s.charAt(0).toUpperCase() + s.slice(1)
}

export function getProfile() {
	return get("user")
}

function profileUpdate(formdata: Omit<IUserInfo, "email" | "profile_image">) {
	return put<IGetUserInfo>("user", formdata)
}

const Profile = () => {
	const queryClient = useQueryClient()

	const {
		data: result,
		isLoading,
		isError,
		error,
		isSuccess,
		isRefetching,
		status
	}: UseQueryResult<AxiosResponse<IGetUserInfo>, AxiosError> = useQuery(
		"profile",
		getProfile
	)
	//Mutation for profile update
	const {
		mutate,
		isLoading: _isLoading,
		isError: _isError,
		error: _error
	}: UseMutationResult<
		IGetUserInfo,
		AxiosError,
		Omit<IUserInfo, "email" | "profile_image">
	> = useMutation<
		IGetUserInfo,
		AxiosError,
		Omit<IUserInfo, "email" | "profile_image">
	>(profileUpdate, {
		onSuccess: () => {
			queryClient.invalidateQueries("profile")
		}
	})

	const userInfo = result?.data?.user_info
	const firstName = userInfo?.first_name
	const lastName = userInfo?.last_name
	const profileImage = userInfo?.profile_image || ""

	const { handleSubmit, control, reset, ...rest } = useForm<IFormInput>({
		defaultValues: values,
		resolver: yupResolver(profileSchema)
	})

	const fullName = `${capitalize(firstName)}  ${capitalize(lastName)}` || ""

	useEffect(() => {
		if (isSuccess) {
			//resetting because of new data
			reset(userInfo)
		}
	}, [isSuccess, reset])

	const onSubmit = handleSubmit(submittedData => {
		const { first_name, last_name, contact_no, address } = submittedData
		mutate({ first_name, last_name, contact_no, address })
	})

	const methods = { handleSubmit, control, reset, ...rest }

	if (isError) {
		return <Box>{error.message}</Box>
	}

	return (
		<FormProvider {...methods}>
			<DevTool control={control} />
			<form onSubmit={onSubmit}>
				<SProfileContainer>
					<SProfileCardContainer>
						<SLoadingBar>
							{(isLoading || _isLoading) && <LinearProgress />}
						</SLoadingBar>
						<SProfileCard>
							<Sidebar
								fullName={fullName}
								isLoading={isLoading}
								profileImage={profileImage}
							/>
							<MainContent isLoading={isLoading} mutationLoading={_isLoading} />
						</SProfileCard>
					</SProfileCardContainer>
				</SProfileContainer>
			</form>
		</FormProvider>
	)
}

export default Profile
