import React, { useRef, useState, useMemo, Children, useEffect, KeyboardEvent, useId } from "react"
import { useSelect, ClickAwayListener } from "@mui/base"
import { Box, Theme } from "@mui/system"
import InputBase from "./InputBase"
import SelectButton from "./SelectButton"
import InputLabel from "./InputLabel"
import { KeyboardArrowDown } from "@mui/icons-material"
import { MenuItem, Popper, SxProps, OutlinedInputProps, Button, Typography, Tooltip, ButtonProps } from "@mui/material"
import Listbox from "./SelectListbox"
import ClearMenuItem from "./ClearMenuItem"

interface ChildProps {
  label: string
  value: string | number
  style?: Record<string, any>
}

export interface SelectProps {
  label?: string | null
  value?: string | null,
  onChange: (value: string | null) => void
  suffix?: string,
  defaultValue?: string,
  children: React.ReactElement<ChildProps>[] | React.ReactElement<ChildProps> | null | undefined,
  sx?: SxProps<Theme>
  placeholder?: string,
  disabled?: boolean,
  buttonProps?: ButtonProps,
  InputComponent?: React.ReactElement<{
    value: string
    onClick: () => void
    endAdornment: OutlinedInputProps["endAdornment"]
    placeholder: string
    disabled: boolean,
    ref: React.MutableRefObject<HTMLElement | undefined>
    sx?: SxProps<Theme>
  } & HTMLElement>
}

type OptionsType = {
  label: string,
  value: string | null
}

const Select = (props: SelectProps) => {
  const { label, children, onChange, suffix, defaultValue, sx, value, InputComponent, placeholder, disabled, buttonProps } = props
  // const inputRef = useRef<HTMLElement>()
  const inputRef = useRef<HTMLButtonElement | null>(null)
  const listBoxRef = useRef<HTMLUListElement>(null)
  const [listboxVisible, setListboxVisible] = useState(false);
  const [displayValue, setDisplayValue] = useState("")
  const uuid = useId()

  const escape = (e: any): void => {
    if(e.code === 'Escape') {
      setListboxVisible(false)
      inputRef.current?.focus()
    }
  }

  const options = useMemo(
    () => {
      if (!children || Children.count(children) <= 0) {
        return []
      } else {
        return Children.map(children, child => {
          if (!child.props.value || !child.props.label) {
            return null
          }
          return { label: child.props.label, value: child.props.value.toString() }
        })
          .filter(opt => opt)
      }
    }, [children]
  )

  const handleNavigation = (e: KeyboardEvent) => {
    e.preventDefault()
    const code = e.code

    if(code === 'Enter') {
      setListboxVisible(true)
    } else if(code === 'ArrowDown' || code === 'ArrowUp') {
      setListboxVisible(true)
      listBoxRef.current?.focus()
    }
  }

  const menuListSelect = (e: KeyboardEvent, fn: Function) => {
    const code = e.code

    if(code === 'Enter') {
      fn(e)
      inputRef.current?.focus()
    }
  }

  const { getButtonProps, getListboxProps, getOptionProps, open } =
    useSelect({
      // listboxRef,
      options: options,
      open: listboxVisible,
      onChange: onChange,
      defaultValue,
      // value
      ...(value !== "" || value !== undefined ? { value } : { value: "" })
    })


  useEffect(() => {
    setDisplayValue(options.find(opt => opt.value == value)?.label || "")
    setListboxVisible(false)
  }, [value])

  const suffixString = suffix ? ` ${suffix}` : ''
  const clearOptionProps = getOptionProps({ value: "", label: "" })

  return (
    <Box display='flex' sx={sx} flexBasis={350}>
      <ClickAwayListener onClickAway={() => setListboxVisible(false)}>
        <Box width='100%'>
          {label && <InputLabel htmlFor={uuid}>{label}</InputLabel>}
          {InputComponent ? <></>
            // React.cloneElement(InputComponent, {
            //   value: `${displayValue}${suffixString}`,
            //   ...(!disabled && { onClick: () => setListboxVisible(true) }),
            //   // onClick: () => setListboxVisible(true),
            //   endAdornment: <KeyboardArrowDown />,
            //   placeholder: placeholder || "",
            //   disabled,
            //   ref: inputRef,
            //   tabIndex: -1
            // }, null)
            :
            <Tooltip title={displayValue || ""}>
              <SelectButton
                id={uuid}
                color='info'
                {...getButtonProps()}
                // value={`${displayValue}${suffixString}`}
                {...(!disabled && { onClick: () => setListboxVisible(true) })}
                endIcon={<KeyboardArrowDown />}
                // placeholder={placeholder || ""}
                disabled={disabled}
                ref={inputRef}
                // tabIndex={-1}
                onKeyUp={handleNavigation}
                onKeyDown={(e: React.KeyboardEvent<any>) =>{
                  if(e.code === 'Tab') {
                    setListboxVisible(false) 
                  }
                  escape(e)
                }}
                role='listbox'
                {...buttonProps}
              >
                <Typography variant="body1">
                  {`${displayValue ? `${displayValue}${suffixString}` : placeholder || ""}`}
                </Typography>
              </SelectButton>
            </Tooltip>
          }
          {
            <Popper open={open} anchorEl={inputRef.current} placement='left-end'>
              <Listbox
                {...getListboxProps()}
                ref={listBoxRef}
                style={{ width: inputRef?.current?.clientWidth }}
                onKeyDown={(e) =>{
                  if(e.code === 'Tab') {
                    setListboxVisible(false)
                    inputRef.current?.focus?.() 
                  }
                  escape(e)
                }}>
                <ClearMenuItem
                  onKeyUp={(e: KeyboardEvent) => menuListSelect(e, clearOptionProps.onClick)}
                  {...clearOptionProps}
                />
                {children && Children.map(children, (child) => {
                  const optionProps = getOptionProps({ value: child.props.value.toString(), label: child.props.label })

                  if (!child.props.value || !child.props.label) {
                    return null
                  }
                  return (
                    <MenuItem
                      key={child.props.value}
                      onKeyUp={(e: KeyboardEvent) => menuListSelect(e, optionProps.onClick)}
                      {...optionProps}
                    >
                      <Tooltip title={child.props.label} placement='right-end'>
                        {React.cloneElement(child, {
                          style: {
                            width: '100%',
                            whiteSpace: 'nowrap',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            textAlign: 'left'
                          }
                        })}
                      </Tooltip>
                    </MenuItem>
                  )
                })}
              </Listbox>
            </Popper>
          }
        </Box>
      </ClickAwayListener>
    </Box>
  )
}

Select.defaultProps = {
  disabled: false
}

export default Select