import {
	Avatar,
	Badge,
	Box,
	Link,
	IconButton,
	Typography,
	Skeleton,
	CircularProgress,
	CircularProgressProps,
	SvgIconProps,
	SvgIconTypeMap
} from "@mui/material"
import {
	AddCircleIcon,
	LockIcon,
	PersonIcon,
	SettingsIcon
} from "../../utils/Icons"
import {
	SListContainer,
	SListItem,
	SProfileImage,
	SProfileImageContainer,
	SProfileSideBar
} from "./index.styles"
import { IImage, IImageUploadResponse, ISideBar } from "./Profile.types"
import { AxiosError, AxiosResponse } from "axios"
import { put } from "../../api/utils"
import { useMutation, UseMutationResult, useQueryClient } from "react-query"
import { useAppContext } from "../../context/AppContext/AppContext"
import { OverridableComponent } from "@mui/material/OverridableComponent"

type MenuListType = {
	icon: OverridableComponent<SvgIconTypeMap<{}, "svg">>,
	label: string,
	to: string,
	isActive: boolean
}

const menuList: MenuListType[] = [
	{
		icon: SettingsIcon,
		label: "Account Settings",
		to: "/profile",
		isActive: true
	},
	{
		icon: PersonIcon,
		label: "Personal Information",
		to: "/profile",
		isActive: false
	},
	{
		icon: LockIcon,
		label: "Privacy",
		to: "/profile",
		isActive: false
	}
]

let activeStyle = {
	textDecoration: "underline"
}

function imageUpload(imageData: IImage) {
	const formData = new FormData()
	formData.append("image", imageData.image as Blob)
	return put<AxiosResponse<IImageUploadResponse>>(
		"user/profile-image",
		formData
	)
}

const Sidebar = ({ profileImage, fullName, isLoading }: ISideBar) => {
	const appState = useAppContext()
	const queryClient = useQueryClient()

	//mutation to update the Image
	const {
		data: ImageData,
		mutate,
		isLoading: isProfileLoading
	}: UseMutationResult<
		AxiosResponse<IImageUploadResponse>,
		AxiosError<IImageUploadResponse>,
		IImage
	> = useMutation<
		AxiosResponse<IImageUploadResponse>,
		AxiosError<IImageUploadResponse>,
		IImage
	>(imageUpload, {
		onSuccess: res => {
			console.log("Image Uploaded Successfully")
			const message = res.data.message
			queryClient.invalidateQueries("profile")

			appState?.setAlert({
				show: true,
				message,
				status: "success"
			})
		},
		onError: err => {
			console.log("Error", err.response?.data)
			const message = err.response?.data?.message
			if (!message) return
			appState?.setAlert({
				show: true,
				message,
				status: "error"
			})
		}
	})

	const handleFileUpload: React.ChangeEventHandler<
		HTMLInputElement
	> = async e => {
		if (e?.target) {
			const image = e.target.files?.[0]
			if (image) {
				mutate({ image })
			}
		}
	}
	return (
		<SProfileSideBar>
			<SProfileImageContainer>
				<SProfileImage>
					{isLoading && (
						<Box>
							<Skeleton
								animation="wave"
								variant="circular"
								width={100}
								height={100}
							/>
						</Box>
					)}
					{!isLoading && (
						<Badge
							overlap="circular"
							anchorOrigin={{ horizontal: "right", vertical: "top" }}
							badgeContent={
								<IconButton size="small" color="primary" component='label' htmlFor="upload-photo-fahad" tabIndex={-1}>
									<input
										id="upload-photo-fahad"
										accept=".png, .jpg, .jpeg, .webp"
										type="file"
										onChange={handleFileUpload}
										style={{ height: 0, width: 0 }}
										tabIndex={0}
										aria-label='Select picture to upload'
									/>
									<AddCircleIcon width={28} height={28} />
								</IconButton>
							}
						>
							<CircularProgressWithChildren isLoading={isProfileLoading}>
								<Avatar
									sx={{ width: 100, height: 100 }}
									src={
										`${process.env.REACT_APP_SERVER_URL}/${profileImage}` ||
										"https://picsum.photos/200"
									}
								/>
							</CircularProgressWithChildren>
						</Badge>
					)}
				</SProfileImage>
				<Typography color="primary" variant="subtitle1" fontWeight="bold">
					{isLoading ? <Skeleton animation="wave" width={100} /> : fullName}
				</Typography>
			</SProfileImageContainer>
			<SListContainer role='tablist'>
				{menuList.map(({ icon : Icon, label, to, isActive }, id) => (
					<SListItem key={id}>
						{<Icon color={ isActive ? 'action' : 'disabled' } />}
						<Box>
							<Link
								role='tabpanel'
								disabled={!isActive}
								component="button"
								// component={RouterLink}
								color='grey.700'
								underline="none"
								variant="body1"
								// to={to}
								fontWeight="600"
								key={label}
								sx={theme =>
									({
										textAlign: 'left',
										"&[disabled]": {
										color: theme.palette.text.mute,
										cursor: "default",
										"&:hover": {
											textDecoration: "none"
										}
									},
									[theme.breakpoints.down('sm')]: {
										textAlign: 'center',
									}
								})}
							>
								{label}
							</Link>
						</Box>
					</SListItem>
				))}
			</SListContainer>
		</SProfileSideBar>
	)
}

export default Sidebar

function CircularProgressWithChildren({
	isLoading,
	...rest
}: CircularProgressProps & {
	isLoading: boolean
	children: React.ReactNode
}) {
	return (
		<Box sx={{ position: "relative", display: "flex", alignItems: "center" }}>
			<CircularProgress
				thickness={1}
				size={110}
				variant="indeterminate"
				style={{ visibility: isLoading ? "visible" : "hidden" }}
				{...rest}
			/>

			<Box
				sx={{
					top: 0,
					left: 5,
					bottom: 0,
					right: 0,
					position: "absolute",
					display: "flex",
					alignItems: "center"
				}}
			>
				{rest.children}
			</Box>
		</Box>
	)
}
