import { TextField } from "@mui/material"
import InputStatus from "../InputStatus"
import { Controller, useFormContext } from "react-hook-form"
import { IFormInput } from "./FormInput.types"

const FormInput = ({ name, ...rest }: IFormInput) => {
	const { control } = useFormContext()
	return (
		<Controller
			control={control}
			name={name}
			render={({
				field,
				fieldState,
				fieldState: { error },
				formState,
				formState: { errors }
			}) => {
				return (
					<TextField
						{...rest}
						{...field}
						// value={value}
						// onChange={onChange}
						// onBlur={onBlur}
						error={!!error}
						helperText={
							errors?.[name] && <InputStatus text={errors?.[name]?.message} />
						}
					/>
				)
			}}
		/>
	)
}

export default FormInput
