import { Box, Typography } from "@mui/material"
import React, { useEffect, useState } from "react"
import ScrollableStack from "../../layout/ScrollableStack"
import { IRedirectToSp } from "./Authcomponents.types"

const RedirectToSp = ({ redirect_url, saml_response, formRef }: IRedirectToSp) => {
	const [timer, setTimer] = useState(10)

	useEffect(() => {
		const clearFormTimer = setTimeout(() => {
			console.log("Timer: ", formRef.current)
			formRef.current?.submit()
		}, 10000)

		const clearTimer = setInterval(() => {
			setTimer((prevTimer) => {
				if(prevTimer === 0) {
					clearTimeout(clearTimer)
				}
				return prevTimer - 1
			})
		}, 1000)

		return () => {
			clearInterval(clearTimer)
			clearTimeout(clearFormTimer)
		}
	}, [])

	return (
		<Box width="100%" textAlign={"center"}>
			<ScrollableStack spacing={2}>
				<Box></Box>
				<Typography variant="body1">
					You are being redirected, please wait...
				</Typography>
				<Box></Box>
				<Typography>
					If you haven't been redirected in {timer} seconds please
				</Typography>
				<Typography>click continue</Typography>
				<Box></Box>
				<Box></Box>
				<input type="hidden" name="SAMLResponse" value={saml_response} />
				<input type="hidden" name="RelayState" value="/whoami" />
			</ScrollableStack>
		</Box>
	)
}

export default RedirectToSp
